.Account .forms {
	align-items: flex-start;
}
.Account .forms .edit-left {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	flex-grow: 2;
}
.Account .forms .edit-right {
	flex-grow: 1;
	align-items: flex-start;
	margin-left: 10%;
}
.Account .forms .edit-right button {
	margin: 10% auto 25%;
}

.Account .forms .edit-bottom {
	margin-top: 2%;
}

.FormEditAccount {
	width: 100%;
}
.FormEditAccount .profile-logo-container {
	margin: 10% 0;
}
.FormEditAccount .form {
	margin: 4% 0;
	align-items: flex-start;
}

.FormEditAccount .form .file-cta {
	background: none;
	border: none;
	font-weight: 500;
	font-size: 0.8em;
	padding: 0;
}

.FormEditAccount .photo-edit {
	flex-grow: 1;
	position: relative;
}
.FormEditAccount .photo-edit .preview {
	position: absolute;
	left: 0;
}

.FormEditAccount .infos-edit {
	flex-grow: 3;
}

.FormDeleteAccount p {
	margin: 5% 0;
}

@media screen and (max-width: 650px) {
	.Account {
		width: 100%;
	}
	.Account .forms .edit-bottom {
		margin: 5% auto 15%;
		align-self: center;
	}
	.Account .forms .edit-right {
		align-items: flex-start;
		margin-left: 0;
	}
	.Account .forms .edit-left {
		width: 90%;
	}
	.FormEditAccount {
		width: 90%;
		margin: 5% auto;
	}
	.FormEditAccount .form {
		width: 80%;
		margin: auto;
	}
}
