.LocationAutoComplete {
	position: relative;
	z-index: 50;
}

.LocationAutoComplete-results {
	position: absolute;
	background-color: white;
	padding: 0;
	margin: 0;
	border: 1px solid #00000011;
}

.LocationAutoComplete-items {
	list-style: none;
	border: 1px solid #00000011;
	cursor: pointer;
	padding: 10px 10px;
}

.LocationAutoComplete-items:hover {
	background-color: #0000ff11;
}

/* SIGN UP PAGE --------------------------  */

.Signup .radio {
  width: 30vw;
}
.Signup .radio-inputs {
  margin-bottom: 3%;
  width: 100%;
  justify-content: space-between;
}

.Signup .radio-inputs input {
  margin-right: 5%;
}

/* FORM SIGN UP ------------------------------- */

.FormSignup {
  width: 70%;
  margin: auto;
  padding: 3%;
}

.Signup .content {
  margin: 5% auto 0;
}

.FormSignup .form-section-bottom,
.FormSignup .btn-signup {
  margin: 6% 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.FormSignup.Player {
  border: 5px solid #00d1b2;
}
.FormSignup.Club {
  border: 5px solid #bd6877;
}

.email {
  padding-left: 40px;
  padding-top: 8px;
}

@media screen and (max-width: 650px) {
  .FormSignup {
    width: 100%;
  }
  .Signup .radio {
    margin-bottom: 4%;
  }
}

/* FORMS --------------------------  */

.other {
  margin: 20px auto 0;
}

.has-name {
  margin-bottom: 30px;
}

.preview {
  width: 50%;
  margin: 0 auto 30px;
}

.field-body {
  margin-top: 10px;
  margin-bottom: 10px;
}

.inter-title {
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: 0;
}

.addTraining {
  margin-bottom: 30px;
}

.delete-btn {
  text-align: center;
  padding-top: 30px;
}

.delete-btn span {
  font-size: 0.5rem;
  display: block;
}

.delete-btn svg {
  color: rgb(247, 92, 92);
}

/* ADD PLAYER FORM --------------- */
#checkbox-player {
  margin-bottom: 30px;
  width: 400px;
}

.checkbox-image {
  height: 30px;
}

#checkbox-player input {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
}

@media screen and (max-width: 400px) {
  #checkbox-player {
    width: 200px;
  }
}

.Card {
	width: 100%;
	height: 100%;
}

.Card:hover {
	
}

.Card.club {
	background-color: var(--color-secondary);
}

.Card .card-image-box {
	height: 30%;
	padding-top: 5%;
}

.Card .image {
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	height: 100%;
}

.Card .card-content {
	height: 70%;
}

.Card .heading {
	margin-bottom: 10px;
	text-transform: capitalize;
	font-weight: 800;
}

.Card .heading .name {
	font-size: 1.3em;
}

.Card .heading .team-owner {
	font-size: 1.1em;
	color: var(--color-main);
}

.Card .address {
	font-size: 0.7em;
	color: var(--color-grey);
	margin-bottom: 10px;
}

.Card .description,
.Card .sport {
	font-size: 0.9em;
	overflow: hidden;
	text-overflow: ellipsis;
	height: 40%;
}

@media screen and (max-width: 1100px) {
	.cards-container.grid {
		width: 90%;
	}
}

@media screen and (max-width: 1100px) and (min-width: 650px) {
	.Card {
		min-width: 250px;
		margin: auto;
	}
	.Card .heading {
		/* font-size: 0.8em; */
		overflow: hidden;
	}
	.Card .address {
		/* font-size: 0.7em; */
	}
	.Card .description,
	.Card .sport {
		/* font-size: 0.8em; */
		overflow: hidden;
		text-overflow: ellipsis;
	}
}

@media screen and (max-width: 650px) {
	.Card {
		max-width: 250px;
		margin: auto;
	}

	.Card .heading .name {
		/* font-size: 1.2em; */
	}

	.Card .heading .team-owner {
		/* font-size: 0.9em; */
		color: var(--color-main);
	}

	.Card .address {
		/* font-size: 0.8em; */
		color: var(--color-grey);
	}

	.Card .description,
	.Card .sport {
		height: 30%;
		/* font-size: 0.9em; */
		overflow: hidden;
		text-overflow: ellipsis;
	}
}

@media screen and (max-width: 500px) {
	.cards-container.grid {
		width: 100%;
	}

